.page-header {
  display:flex;
  flex-direction: column;
  margin:0 auto;
  width: 320px;
}
.page-header__logo {
  order: 2;
  margin: 0 auto;
  z-index: 1;
}
@media (min-width: @tablet-width) {
.page-header {
  position: relative;
  width: @tablet-width;
  }
  .page-header__logo {
   position: absolute;
   top:0;
   left: 333px;
  }

}

@media (min-width:@desktop-width) {
.page-header {
  position: relative;
  width: @desktop-width;
  }
  .page-header__logo .page-header__logo-image{
    position: absolute;
    top: 0;
    left: 174px;
    z-index : 1;

    display: block;
    width  : 188px;
    height : 96px;

  }

}
