.sedona-advantages {
  width: 320px;
  margin: 0 auto;
}

.sedona-advantages__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
.sedona-advantages__item {

  background-color: @color-gray-background;
  border-bottom: 1px solid #d5d5d5;
}
.sedona-advantages__area {
  margin: 0;
  padding-top: 25px;
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  text-align: center;
}
.sedona-advantages__road {
  margin: 0;
  padding-top: 29px;
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  text-align: center;
}
.sedona-advantages__tourist {
  margin: 0;
  padding-top: 26px;
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  text-align: center;
}
.sedona-advantages__number {
  margin: 0;
  padding-top: 10px;
  font-size: 14px;
  line-height: 21px;
  color: @color-gray-color;
  text-align: center;
}

.sedona-advantages__text {
  padding-left: 32px;
  padding-right: 33px;
  padding-bottom: 14px;
  font-size: 14px;
  line-height: 21px;
  color: @color-gray-color;
  text-align: center;
}
.sedona-advantages__text-road {
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 14px;
  font-size: 14px;
  line-height: 21px;
  color: @color-gray-color;
  text-align: center;
}

@media(min-width:@tablet-width){
  .sedona-advantages {
    width: @tablet-width;
  }
  .sedona-advantages__list {
    display: flex;
  }
  .sedona-advantages__item {
    width: 256px;
  }
  .sedona-advantages__area {
    margin: 0;
    padding-top: 44px;
    padding-left: 20px;
    padding-right: 18px;
    font-size: 21px;
    text-align: center;
  }
  .sedona-advantages__road {
    margin: 0;
    padding-top: 44px;
    padding-left: 56px;
    padding-right: 57px;
    font-size: 21px;
    text-align: center;
  }
  .sedona-advantages__tourist {
    margin: 0;
    padding-top: 44px;
    padding-left: 64px;
    padding-right: 64px;
    font-size: 21px;
    text-align: center;
  }

  .sedona-advantages__number {
    margin: 0;
    padding-top: 11px;
    font-size: 14px;
    line-height: 21px;
    color: @color-gray-color;
    text-align: center;
  }

  .sedona-advantages__text {
    padding-top: 8px;
    padding-left: 43px;
    padding-right: 43px;
    padding-bottom: 30px;
    font-size: 14px;
    line-height: 21px;
    color: @color-gray-color;
    text-align: center;
  }
  .sedona-advantages__text-road {
    padding-top: 8px;
    padding-left: 51px;
    padding-right: 54px;
    padding-bottom: 30px;
    font-size: 14px;
    line-height: 21px;
    color: @color-gray-color;
    text-align: center;
  }
}

@media(min-width:@desktop-width){
  .sedona-advantages {
    width: @desktop-width;
  }
