    padding-top: 44px;
    padding-left: 93px;
    padding-right: 94px;
    font-size: 21px;
    text-align: center;
  }
  .sedona-advantages__road {

    padding-top: 44px;
    padding-left: 111px;
    padding-right: 112px;
    font-size: 21px;
    text-align: center;
  }
  .sedona-advantages__tourist {

    padding-top: 44px;
    padding-left: 128px;
    padding-right: 128px;
    font-size: 21px;
    text-align: center;
  }
  .sedona-advantages__number {
    margin: 0;
    padding-top: 12px;
    font-size: 14px;
    line-height: 21px;
    color: @color-gray-color;
    text-align: center;
  }

  .sedona-advantages__text {
    padding-top: 8px;
    padding-left: 43px;
    padding-right: 43px;
    padding-bottom: 30px;
    font-size: 14px;
    line-height: 21px;
    color: @color-gray-color;
    text-align: center;
  }
  .sedona-advantages__text-road {
    padding-top: 8px;
    padding-left: 60px;
    padding-right: 60px;
    padding-bottom: 30px;
    font-size: 14px;
    line-height: 21px;
    color: @color-gray-color;
    text-align: center;
  }
}
